import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MultipleApplicationsComponent } from './multiple-applications.component';

const routes: Routes = [
  {
    path:'',
    component: MultipleApplicationsComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MultipleApplicationsRoutingModule { }
